<template>
  <div id="right">
    <h3>회원 정보를 수정할 수 있습니다.</h3>
    <p>수정사항</p>

    <label for="이름">이름 : </label>
    <input type="text" v-model="user.name"><br>
    <label for="주소">주소 : </label>
    <input type="text" v-model="user.address"><br>
    <label for="전화번호">전화번호 : </label>
    <input type="text" v-model="user.phone"><br>
    <button @click="changeUser">수정완료</button>
  </div>
</template>
<script>
export default {
  props: [
    'name',
    'address',
    'phone'
  ],
  data() {
    return {
      user: {}
    }
  },
  created() {
    // 재할당을 통해 user Object로 넣어줌
    this.user.name = this.name
    this.user.address = this.address
    this.user.phone = this.phone
  },
  methods: {
    changeUser() {
      console.log(this.user)
      //$emit : 부모에게 요청할 때
      this.$emit('child', this.user)
      // data()의 object가 여러개일 경우
      // this.$emit('child', this.user, 1, 2, 3, 4)
      // child(this.user, 1, 2, 3, 4)
    }
  }
}
</script>
<style scoped>
#right {
  width: 50%;
  background: plum;
  float: left;
}
</style>